import React, { Component, createRef } from 'react';
import { not_close_tags } from '../../data/dom';
import arrow_src from '../../assets/svg/arrow_dom.svg';
import view_src from '../../assets/svg/view.svg';
import not_eye_src from '../../assets/svg/not_eye.svg';
import inherit_unvisible_src from '../../assets/svg/inherit_unvisible.svg';
import classnames from 'classnames';
import { connect } from 'react-redux';
import { ThemeStore, Turl } from '../../redux/interfaces';
import * as Action from '../../redux/actions/actions';
import * as Clear from '../../redux/actions/clear_actions';
import * as Helper from '../../helper/helper';
import Label from './Label';
import LayerDnd from './LayerDnd';

export interface ILayer {
  childs_unvisible: boolean;
  parent: boolean;
  changeVisible: (bool: boolean) => void;
  changeOpen: (bool: boolean) => void;
  url: Turl;
  last?: boolean;
}

interface StateProps {
  active: boolean;
  item: any;
  dnd: boolean;
}

interface DispatchProps {
  change_active: (mas: Turl) => void;
  cl_change_active: (mas: Turl[]) => void;
  change_visible: (mas: any, value: boolean) => void;
  change_item: (url: Turl, label: string) => void;
  change_dnd: (bool: boolean) => void;
  change_open: (mas: any, value: boolean) => void;
  cx_handle_mouseup_layer: (url: any[], position: string) => void;
  cx_active_mouseup: (url: Turl) => void;
}

type Props = ILayer & StateProps & DispatchProps;

const styleOpenArrow = {
  transform: 'rotate(90deg)',
};

interface IState {
  edit: boolean;
  active: boolean;
  open: boolean;
  visible: boolean;
  dnd: boolean;
  hover: boolean;
  position: string;
}

class Layer extends Component<Props> {
  constructor(props: Props) {
    super(props);

    this.changeOpen = this.changeOpen.bind(this);
    this.changeVisible = this.changeVisible.bind(this);
    this.changeActive = this.changeActive.bind(this);
    this.handleMouseDown = this.handleMouseDown.bind(this);
    this.handleMouseUp = this.handleMouseUp.bind(this);
    this.docmousedown = this.docmousedown.bind(this);
    this.changeLabel = this.changeLabel.bind(this);
    this.mouseUpSeparator = this.mouseUpSeparator.bind(this);
  }
  // State
  state: IState = {
    edit: false,
    active: false,
    open: this.props.item.open,
    visible: this.props.item.visible,
    dnd: this.props.dnd,
    position: '',
    hover: false,
  };

  // own options
  arrow: any = null;
  view: any = null;
  classnames: string = '';
  label: string = '';
  style: any = {};
  labelRef: any = createRef();
  layerDnd: any = null;

  componentDidMount() {
    let { active } = this.props;
    if (this.state.active !== active) {
      this.setState({ active });
    }
  }

  componentWillMount() {
    this.conversion(this.props, this.state);
  }

  shouldComponentUpdate(nextProps: Props, nextState: IState) {
    let bool = false;
    let b = true;

    this.conversion(nextProps, nextState);

    if (props_equal(this.props, nextProps)) {
      if (state_equal(this.state, nextState)) b = false;
    }

    if (!b) {
      return false;
    }

    let { active, childs_unvisible, item } = nextProps;

    if (this.props.active !== active) {
      this.setState({ active });
    }

    if (item) {
      this.conversion(nextProps, nextState);
      if (!item_equal(this.props.item, item)) {
        bool = true;
        let state_obj: any = {};

        if (item.visible !== nextState.visible) {
          this.props.changeVisible(item.visible);
          state_obj['visible'] = item.visible;
        }

        if (item.open !== nextState.open) {
          this.props.changeOpen(item.open);
          state_obj['open'] = item.open;
        }
        if (
          state_obj['visible'] !== undefined ||
          state_obj['open'] !== undefined
        ) {
          bool = false;
          this.setState(state_obj);
        }
      }
    }

    if (this.props.dnd !== nextProps.dnd) {
      this.zeroing_functions();
      this.setState({ dnd: nextProps.dnd });
    }

    if (this.state !== nextState) {
      this.conversion(nextProps, nextState);
      bool = true;
    }

    if (this.props.childs_unvisible !== childs_unvisible) {
      this.conversion(nextProps, nextState);
      bool = true;
    }

    return bool;
  }

  changeLabel(label: string) {
    this.label = label;
  }

  handleMouseUp() {
    this.props.cx_handle_mouseup_layer(this.props.url, this.state.position);
  }

  conversion(props: Props, state: IState) {
    let { item, childs_unvisible, parent, url, dnd } = props;
    let { edit, active, open, visible, position, hover } = state;

    let arr_pl = url.filter((item) => item === 'children');

    let pl = (arr_pl.length - 1) * 10;

    let { attributes, tag, textContent, children_size } = item;

    // Arrow
    let arrow = (
      <div className="arrow_wrap" onMouseDown={this.changeOpen}>
        <div className="arrow">
          <img src={arrow_src} style={open ? styleOpenArrow : {}} alt="Arrow" />
        </div>
      </div>
    );
    this.arrow = parent && children_size !== 0 ? arrow : null;

    // View
    let view = visible
      ? childs_unvisible
        ? inherit_unvisible_src
        : view_src
      : not_eye_src;

    this.view = <img src={view} alt="visible" />;

    // Classnames
    this.classnames = classnames({
      visible: visible,
      unvisible: !visible,
      childs_unvisible,
      edit,
      active,
      dnd,
      center: hover && position === 'center' ? true : false,
      parent,

      'no-hover': hover,
    });
    this.classnames += ' ' + props.url.join('-');

    // Label
    // -----Attributes

    if (item) {
      let attrs = '';
      if (attributes) {
        for (let key in attributes) {
          attrs += ' ' + key + '="' + attributes[key] + '"';
        }
      }
      // -----Tag
      let label = '';
      if (tag) {
        let bool_close_tag = not_close_tags.some((item) => item === tag);
        let close_tag = bool_close_tag ? '/>' : '>';

        //    <div class="block">
        label = '<' + tag + attrs + close_tag;

        //    textContent  <div class="block">Test
        if (textContent) {
          label += textContent;
        }

        //           <div class="block">Test</div>
        if (!open && textContent && !bool_close_tag) label += '</' + tag + '>';

        //           <div class="block">Test</div>
        if (open && textContent && !bool_close_tag && children_size === 0)
          label += '</' + tag + '>';

        //             0  <div class="block">...</div>
        if (!open && !textContent && !bool_close_tag && children_size > 0)
          label += `...</${tag}>`;

        //           <div class="block">...</div>
        if (!textContent && !bool_close_tag && children_size === 0)
          label += `</${tag}>`;
      } else {
        if (textContent) label += textContent;
      }

      this.label = label;
    }
    // Margin-left
    this.style = {
      marginLeft: pl + 'px',
    };

    // for dnd
    if (props.dnd) {
      if (children_size > 0) {
        let dnd_props = {
          url,
          type: 'parent',
          top: true,
          bottom: open === false && this.props.last ? true : false,
        };
        this.layerDnd = <LayerDnd {...dnd_props} />;
      } else {
        let dnd_props = {
          url,
          type: 'simple',
          top: true,
          bottom: this.props.last === true ? true : false,
          parent,
          last: this.props.last ? true : false,
        };
        this.layerDnd = <LayerDnd {...dnd_props} />;
      }
    } else {
      this.layerDnd = null;
    }
  }

  mouseUpSeparator(position: string) {
    this.props.cx_handle_mouseup_layer(this.props.url, position);
  }

  changeOpen(e: any) {
    Helper.pauseEvent(e);
    let open = this.state.open ? false : true;
    this.setState({ open });

    this.props.changeOpen(open);
    this.props.change_open(this.props.url, open);
  }

  zeroing_functions() {
    let elem = this.labelRef.current;

    elem.onmousemove = null;
    elem.onmouseout = null;
  }

  changeVisible(e: any) {
    Helper.pauseEvent(e);
    let { visible } = this.state;
    this.setState({ visible: !visible });
    if (this.props.parent) this.props.changeVisible(!visible);
    this.props.change_visible(this.props.url, !visible);
  }

  timer = setTimeout(() => {}, 500);

  mousedowncount = 0;
  handler: boolean = false;

  changeActive(e: any) {
    if (
      !e.target.classList.contains('arrow_wrap') &&
      !e.target.classList.contains('view_wrap') &&
      e.target.tagName !== 'IMG'
    ) {
      this.setState({ active: true });
      let { url } = this.props;
      this.props.change_active(url);
    }
  }

  docmousedown(e: any) {
    if (e.target.tagName !== 'TEXTAREA') {
      this.props.change_item(this.props.url, this.label);
      this.setState({ edit: false });
      this.mousedowncount = 0;
      document.onmousedown = null;
    }
  }

  handleMouseDown(e: any) {
    let elem = this.labelRef.current;
    if (!this.state.active) this.mousedowncount = 0;
    this.mousedowncount++;

    document.onselectstart = () => {
      return false;
    };

    let b = true;
    if (this.props.item.children_size > 1 && !this.state.open) b = false;

    //    
    if (this.mousedowncount > 1 && b) {
      clearTimeout(this.timer);
      if (!this.state.edit) this.setState({ edit: true });

      if (!this.handler) {
        document.addEventListener('mousedown', this.docmousedown, true);
        this.handler = true;
      }
    } else {
      if (
        !e.target.classList.contains('arrow_wrap') &&
        !e.target.classList.contains('view_wrap') &&
        e.target.tagName !== 'IMG'
      ) {
        this.setState({ active: true });
        let { url } = this.props;

        this.props.change_active(url);

        this.timer = setTimeout(() => {
          this.mousedowncount = 0;
        }, 300);

        if (this.handler) {
          document.removeEventListener('mousedown', this.docmousedown, true);
          this.handler = false;
        }

        elem.onmouseup = (c: any) => {
          this.props.cx_active_mouseup(this.props.url);
        };
      }
    }

    let c = true;
    if (c) {
      elem.onmousemove = (m: any) => {
        document.onselectstart = (z: any) => {
          z.preventDefault();
        };

        elem.onmouseout = () => {
          this.props.change_dnd(true);
          c = false;
        };
      };

      document.onmouseup = () => {
        elem.onmousemove = null;
        elem.onmouseout = null;
        this.props.change_dnd(false);
      };
    }
  }

  render() {
    let { view, classnames, label, style, arrow, changeVisible } = this;

    return (
      <li
        className={classnames}
        ref={this.labelRef}
        onMouseDown={this.handleMouseDown}
      >
        <div className="opacity_wrap">
          <div className="layer_left" style={style}>
            <div className="layer_wrap"></div>
            {arrow}
            <Label
              label={label}
              edit={this.state.edit}
              changeLabel={this.changeLabel}
            />
            {this.layerDnd}
          </div>
        </div>
        <div className="view_wrap" onMouseDown={changeVisible}>
          <div className="view">{view}</div>
        </div>
      </li>
    );
  }
}

const mapStateToProps = (
  { active_url, dom, dnd }: ThemeStore,
  ownProps: ILayer,
) => {
  let { url } = ownProps;
  let item: any = false;
  let active = false;
  let elem = dom.getIn(url);

  if (elem) {
    item = elem.delete('children').toJS();
    if (elem.has('children')) {
      item.children_size = elem.get('children').size;
    }

    active_url.forEach((active_item: any[]) => {
      if (ownProps.url.join(',') === active_item.join(',')) {
        active = true;
      }
    });
  }

  return {
    active,
    dnd,
    item,
  };
};

const mapDispatchToProps = {
  change_active: Action.cx_change_active,
  cl_change_active: Clear.change_active,
  change_visible: Action.change_visible,
  change_item: Action.change_item,
  change_open: Action.change_open,
  change_dnd: Clear.change_dnd,
  cx_handle_mouseup_layer: Action.cx_handle_mouseup_layer,
  cx_active_mouseup: Action.cx_active_mouseup,
};

export default connect(mapStateToProps, mapDispatchToProps)(Layer);

//  2 item
function item_equal(obj1: any, obj2: any) {
  let arr1 = Object.entries(obj1);
  let arr2 = Object.entries(obj2);

  if (arr1.length !== arr2.length) return false;

  for (let key in obj1) {
    if (key === 'attributes') {
      if (obj2[key] === undefined) return false;

      for (let key2 in obj1[key]) {
        if (obj1[key][key2] !== obj2[key][key2]) return false;
      }
    } else if (obj1[key] !== obj2[key]) return false;
  }

  return true;
}

//  2 state
function state_equal(state1: any, state2: any) {
  if (Object.entries(state1).length !== Object.entries(state2).length)
    return false;

  for (let key in state1) {
    if (state1[key] !== state2[key]) return false;
  }

  return true;
}

//  2 props
function props_equal(props: any, nextProps: any) {
  if (props.active !== nextProps.active) return false;

  if (props.childs_unvisible !== nextProps.childs_unvisible) return false;
  if (props.parent !== nextProps.parent) return false;
  if (props.url !== nextProps.url) return false;
  if (props.dnd !== nextProps.dnd) return false;
  if (!state_equal(props.item, nextProps.item)) return false;

  return true;
}
