import { List, fromJS } from 'immutable';
import * as Helper from '../helper/helper';
import { Turl, Tto } from '../redux/interfaces';

//       
export const is_active = (url: Turl, active_url: Turl[]) => {
  for (let i = 0; i < active_url.length; i++) {
    if (Helper.arr_equal(active_url[i], url)) return true;
  }

  return false;
};

//      : top or bottom
const insert_element = (position: string) => (
  url: Turl,
  dom: any,
  active_url: any,
) => {
  let nw_url = url.slice();

  let index = Number(nw_url.pop()); //   children
  let list = dom.getIn(nw_url).toArray(); //
  let list2 = [];
  let active_url2: any = [];
  let indexs: number[] = [];

  let nw_dom = dom;
  let bool = false;

  for (let i = 0; i < index; i++) {
    bool = is_active([...nw_url, i], active_url);
    if (!bool) list2.push(list[i]);
  }

  if (position === 'bottom') {
    bool = is_active([...nw_url, index], active_url);
    if (!bool) list2.push(list[index]);
  }

  active_url.forEach((active_item: any) => {
    list2.push(dom.getIn(active_item));
    indexs.push(list2.length - 1);
  });

  let w = position === 'top' ? index : index + 1;

  for (let i = w; i < list.length; i++) {
    let bool = is_active([...nw_url, i], active_url);
    if (!bool) list2.push(list[i]);
  }

  let list3 = fromJS(list2);

  //    
  let actvs_right = url_sort(active_url);

  let calc_to = calculate_to(actvs_right, { to: url, position });

  let count_before_to = 0;

  actvs_right.forEach((active_item: any, i: number) => {
    let to_url = [...nw_url];
    let act_url = [...active_item];
    to_url.pop();
    act_url.splice(-2, 2);

    if (Helper.arr_equal(act_url, to_url)) count_before_to++;
  });

  actvs_right.forEach((active_item: any, i: number) => {
    let bool = nw_url.every((item: any, i: number) => item === active_item[i]);
    nw_dom = nw_dom.deleteIn(active_item);

    if (!bool) nw_dom = collapsing_textContent(nw_dom, active_item);
    if (bool) {
      let act_url = [...active_item];

      act_url.splice(0, url.length - 1);

      if (act_url.length > 1) {
        if (act_url[0] < index) {
          list3 = list3.deleteIn(act_url);
        } else {
          if (
            position === 'bottom' &&
            url_comparison_hierarchy(active_item, url, 'bottom') === 1
          ) {
          } else {
            act_url[0] = act_url[0] + actvs_right.length;
          }
          if (actvs_right.length > 1 && count_before_to !== 0) {
            if (position === 'top') {
              act_url[0] = act_url[0] - count_before_to;
            } else if (position === 'bottom') {
              act_url[0] = act_url[0] - actvs_right.length - count_before_to;
            }
          }
          list3 = list3.deleteIn(act_url);
        }
      }
      list3 = collapsing_textContent(list3, act_url);
    }
  });

  indexs.forEach((item: number, i: number) => {
    let temp_arr = [...calc_to];
    temp_arr[temp_arr.length - 1] = item;
    active_url2.push(temp_arr);
  });

  calc_to.pop();
  nw_dom = nw_dom.setIn(calc_to, list3);

  return { nw_dom, active_url2 };
};

export const InsertElement = {
  top: insert_element('top'),
  bottom: insert_element('bottom'),
};

//      id  
function immersion(item: any) {
  item.id = String(Math.random());

  if (item['children']) {
    item['children'].forEach((iter: any) => {
      immersion(iter);
    });
  }
}

//    id  
function getItem(elem: any) {
  let elem2 = elem.toJS();

  immersion(elem2);

  return fromJS(elem2);
}

//         
export const clone_bottom_elements = (
  saved_items: any[],
  dom: any,
  active_url: any[],
) => {
  let transform_save_items: any[] = [];

  saved_items.forEach((save_item) => {
    transform_save_items.push(getItem(save_item));
  });

  let nw_dom = dom;
  let nw_url: any[];
  let list2: any[] = [];
  let list: any[];
  let index = 0;
  let active_url2: any[] = [];

  nw_url = active_url[0].slice();
  index = Number(nw_url.pop());
  list = nw_dom.getIn(nw_url).toArray();

  for (let i = 0; i <= index; i++) {
    list2.push(list[i]);
  }

  transform_save_items.forEach((save_item: any) => {
    list2.push(save_item);
    active_url2.push([...nw_url, list2.length - 1]);
  });

  if (list.length > index + 1) {
    for (let i = index + 1; i < list.length; i++) {
      list2.push(list[i]);
    }
  }

  let list3 = List(list2);

  nw_dom = nw_dom.setIn(nw_url, list3);

  return { nw_dom, active_url2 };
};

//      ,      1     ,       textcontent
const collapsing_textContent = (dom: any, active: Turl) => {
  let nw_dom = dom;
  let parent_arr: Turl;
  let parent: any;
  let textContent: any;

  parent_arr = [...active];
  parent_arr.splice(-1, 1);
  parent = nw_dom.getIn(parent_arr);
  if (parent.size === 1) {
    textContent = parent.get(0).get('textContent');
    if (!parent.get(0).get('tag') && textContent) {
      nw_dom = nw_dom.deleteIn([...parent_arr, 0]);
      parent_arr.splice(-1, 1);

      parent = nw_dom.getIn(parent_arr);

      parent = parent.set('textContent', textContent);
      parent = parent.set('open', false);

      nw_dom = nw_dom.setIn(parent_arr, parent);
    }
  }

  return nw_dom;
};

//    
export const delete_elements = (dom: any, active_url: any[]) => {
  let actvs_right = url_sort(active_url);
  let nw_dom = dom;

  actvs_right.forEach((active: any[]) => {
    nw_dom = nw_dom.deleteIn(active);

    nw_dom = collapsing_textContent(nw_dom, active);
  });

  return { nw_dom };
};

//      ,  
export const length_sort = (array: Turl[]): Turl[] => {
  if (array.length < 2) return array;

  const pivot = array.filter((e) => e.length === array[0].length);
  const greater = array.filter((e) => e.length < array[0].length);
  const less = array.filter((e) => e.length > array[0].length);

  return [...length_sort(less), ...pivot, ...length_sort(greater)];
};

//    url,   ,    
const getIndexDivergency = (from: Turl, to: Turl) => {
  for (let i = 0; i < from.length; i++) {
    if (from[i] !== to[i]) {
      return i;
    }
  }

  return -1;
};

//   to ,    
export const calculate_to = (array: Turl[], to: Tto): any[] | any => {
  if (to instanceof Object) {
    let myto = [...to.to];
    let url_comp = -1;
    let ind_diff = -1;
    array.forEach((active: Turl, i: number) => {
      url_comp = url_comparison(active, myto);

      if (url_comp === 2) {
        ind_diff = getIndexDivergency(active, myto);
        if (ind_diff > 0) {
          if (ind_diff === active.length - 1) {
            myto[ind_diff] = Number(myto[ind_diff]) - 1;
          }
        }
      }
    });

    return myto;
  }

  return to;
};

//  url 
export function url_sort(array: Turl[]) {
  let arr_length = length_sort(array);

  let qw: any[] = [];
  let index = 0;
  let c = false;
  while (arr_length.length !== 0) {
    index = 0;
    arr_length.forEach((item: any[], i: number) => {
      c = false;
      item.forEach((sub_item: any, j: number) => {
        if (!isNaN(sub_item) && !c) {
          c = true;
          if (arr_length[index][j] < sub_item) index = i;
          if (arr_length[index][j] === sub_item) c = false;
        }
      });
    });

    qw.push(arr_length[index].slice());
    arr_length.splice(index, 1);
  }

  return qw;
}

//    shift
const down_move = (
  from: Turl,
  to: Turl,
  base: Turl,
  index: number,
  active_urls: Turl[],
) => {
  base.push('children');

  let temp: any[] = [];

  for (let j = Number(from[index]); j < to[index]; j++) {
    temp = [...base, j];
    active_urls.push(temp);
  }

  base.push(to[index]);

  for (let i = index + 2; i < to.length; i += 2) {
    base.push('children');

    for (let j = 0; j < to[i]; j++) {
      temp = [...base, j];
      active_urls.push(temp);
    }

    base.push(to[i]);
    if (base.length === to.length) active_urls.push(base);
  }
};
//    shift
const up_move = (
  from: Turl,
  to: Turl,
  base: Turl,
  index: number,
  active_urls: Turl[],
  dom: any,
) => {
  base = [...from.slice(0, -1)];
  let end = dom.getIn(base).size;
  let temp: any[];

  for (let j = Number(from[from.length - 1]); j < end; j++) {
    temp = [...base, j];
    active_urls.push(temp);
  }

  base.pop();
  base.pop();

  for (let i = Number(from.length - 3); i > 1; i -= 2) {
    end = dom.getIn(base).size;
    // parent_urls.push([...base]);

    for (let j = Number(from[i]); j < end; j++) {
      if (j !== Number(from[i])) {
        temp = [...base, j];
        active_urls.push(temp);
      }
    }

    base.pop();
    base.pop();
  }

  end = dom.getIn(base).size;

  for (let j = Number(from[index]) + 1; j <= Number(to[index]); j++) {
    temp = [...base, j];
    active_urls.push(temp);
  }
};
//      shift
const cx_move = (
  from: Turl,
  to: Turl,
  index: number,
  active_urls: Turl[],
  dom: any,
) => {
  let base = [...from.slice(0, -1)];
  let end = dom.getIn(base).size;
  let temp: any[];

  for (let j = Number(from[from.length - 1]); j < end; j++) {
    temp = [...base, j];
    active_urls.push(temp);
  }

  base.pop();
  base.pop();

  for (let i = Number(from.length - 3); i > index; i -= 2) {
    end = dom.getIn(base).size;

    for (let j = Number(from[i]); j < end; j++) {
      if (j !== Number(from[i])) {
        temp = [...base, j];
        active_urls.push(temp);
      }
    }

    base.splice(-2, 2);
  }

  for (let j = Number(from[index]) + 1; j < Number(to[index]); j++) {
    temp = [...base, j];
    active_urls.push(temp);
  }

  base.push(to[index]);

  for (let i = index + 2; i < to.length; i += 2) {
    base.push('children');

    for (let j = 0; j < to[i]; j++) {
      temp = [...base, j];
      active_urls.push(temp);
    }

    base.push(to[i]);
    if (base.length === to.length) active_urls.push(base);
  }
};
//      shift
const children_move = (
  from: Turl,
  to: Turl,
  base: Turl,
  index: number,
  active_urls: Turl[],
) => {
  base.push('children');
  let temp: any[];
  for (
    let j = Number(from[from.length - 1]);
    j <= Number(to[to.length - 1]);
    j++
  ) {
    temp = [...base, j];
    active_urls.push(temp);
  }
};

//    url
export const clear_repeat_url = (active_url: Turl[]) => {
  let cp_active_url = active_url.map((item: Turl) => item.join(','));
  let uniqueArray = cp_active_url.filter(function (item, pos) {
    return cp_active_url.indexOf(item) === pos;
  });

  let res = uniqueArray.map((item: string) => item.split(','));

  res.forEach((item: Turl) => {
    for (let i = 0; i < item.length; i++) {
      if (!isNaN(+item[i])) {
        item[i] = Number(item[i]);
      }
    }
  });

  return res;
};

//   
export const delete_active = (url: Turl, active_url: Turl[]) => {
  let indexs: number[] = [];
  let cp_active_url = active_url.map((item: Turl) => item.slice());

  active_url.forEach((item: Turl, i: number) => {
    if (Helper.arr_equal(item, url)) indexs.push(i);
  });

  Helper.sort_num(indexs);

  indexs.forEach((item: number) => {
    cp_active_url.splice(item, 1);
  });

  return cp_active_url;
};

//   shift
export const active_url_shift = (arr1: Turl, arr2: Turl) => (dom: any) => {
  let from: Turl = [];
  let to: Turl = [];
  let comparison = url_comparison(arr1, arr2);

  if (comparison === 2) {
    from = [...arr1];
    to = [...arr2];
  } else if (comparison === 1) {
    from = [...arr2];
    to = [...arr1];
  }

  let index = getIndexDivergency(from, to);

  if (index > -1) {
    let active_urls: Turl[] = [];
    let base: Turl = [...from.slice(0, index - 1)];

    if (from.length < to.length && index === from.length - 1) {
      down_move(from, to, base, index, active_urls);
    } else if (from.length > to.length && index === to.length - 1) {
      up_move(from, to, base, index, active_urls, dom);
    } else if (index === to.length - 1 && index === from.length - 1) {
      children_move(from, to, base, index, active_urls);
    } else {
      cx_move(from, to, index, active_urls, dom);
    }

    return active_urls;
  }

  return [];
};

//  url    
function url_comparison_hierarchy(
  active: any[],
  to: any[],
  position: string,
): number {
  let l = active.length > to.length ? to.length : active.length;

  if (active.length === to.length) return 0;

  for (let i = 0; i < l; i++) {
    if (active[i] !== to[i]) return 0;
  }

  return 1;
}

//   url
export function url_comparison(active: any[], to: any[]): number {
  let l = active.length > to.length ? to.length : active.length;

  for (let i = 0; i < l; i++) {
    if (!isNaN(active[i]) && !isNaN(to[i])) {
      if (active[i] > to[i]) {
        return 1;
      } else if (active[i] < to[i]) {
        return 2;
      }
    }
  }

  return l !== active.length ? 1 : 2;
}
