import React, { Component, createRef } from 'react';
import { Turl } from '../../redux/interfaces';
import { connect } from 'react-redux';
import * as Action from '../../redux/actions/actions';
import Separator from './Separator';

interface ILayerDnd {
  last?: boolean;
  parent?: boolean;
  url: Turl;
  top: boolean;
  bottom: boolean;
  type: string;
}

interface IDispatch {
  cx_handle_mouseup_layer: (url: any[], position: string) => void;
}

type Props = ILayerDnd & IDispatch;

class LayerDnd extends Component<Props> {
  constructor(props: Props) {
    super(props);

    this.TrackMouseUp = this.TrackMouseUp.bind(this);
    this.handleMouseOver = this.handleMouseOver.bind(this);
    this.handleMouseOut = this.handleMouseOut.bind(this);
  }
  height_beforeTop: number = 0;
  height_afterTop: number = 0;
  height_beforeBottom: number = 0;
  height_afterBottom: number = 0;

  labelRef: any = createRef();

  componentDidMount() {
    this.updateLabelOptions(this.props);
    this.forceUpdate();

    let elem = this.labelRef.current;
    elem.onmouseup = (e: any) => {
      e.stopPropagation();
      this.props.cx_handle_mouseup_layer(this.props.url, 'center');
    };
  }

  shouldComponentUpdate(nextProps: Props) {
    this.updateLabelOptions(nextProps);
    return true;
  }

  TrackMouseUp(position: string) {
    this.props.cx_handle_mouseup_layer(this.props.url, position);
  }
  handleMouseOver(e: any) {
    let id = e.target.getAttribute('id');

    if (id === 'layer_overlay') {
      e.target.classList.add('active');
    }
  }
  handleMouseOut(e: any) {
    let id = e.target.getAttribute('id');

    if (id === 'layer_overlay') {
      e.target.classList.remove('active');
    }
  }

  getHeight(height: number) {
    return +Number(height / 3.16).toFixed(2);
  }

  updateLabelOptions(props: Props) {
    let elem = this.labelRef.current;

    let { type, bottom } = this.props;
    // @ts-ignore
    let li = elem.parentNode.parentNode.parentNode;
    let height = elem.offsetHeight;
    let bedoreHeight = 12;
    let res_height = +Number(height / 3.16).toFixed(2);

    if (li.previousSibling !== null) {
      let prev_li = li.previousSibling;

      if (prev_li.classList.contains('sub_dom'))
        prev_li = prev_li.previousSibling;

      let prev_height = prev_li.offsetHeight;

      let prev_parent = prev_li.classList.contains('parent');

      if (prev_parent) {
        bedoreHeight = this.getHeight(prev_height);
      } else {
        bedoreHeight = prev_height / 2;
      }
    } else {
      let sub = li.parentNode;
      if (sub.previousSibling) {
        if (sub.previousSibling.tagName === 'LI') {
          bedoreHeight = this.getHeight(sub.previousSibling.offsetHeight);
        }
      } else bedoreHeight = 0;
    }

    if (type === 'parent') {
      // Top Before

      this.height_beforeTop = bedoreHeight;

      // Top after
      this.height_afterTop = res_height;

      // Bottom
      if (bottom) {
        this.height_beforeBottom = this.height_afterTop;
        this.height_afterBottom = 12;
      }
    }

    if (type === 'simple') {
      // Top before

      this.height_beforeTop = bedoreHeight;

      // Top after
      if (this.props.parent) {
        this.height_afterTop = res_height;
      } else {
        this.height_afterTop = height / 2;
      }

      // Bottom
      if (bottom) {
        if (this.props.parent) {
          this.height_beforeBottom = res_height;
        } else {
          this.height_beforeBottom = height / 2;
        }
        this.height_afterBottom = 12;
      }
    }

    if (type === 'close') {
      // Bottom
      if (bottom) {
        this.height_beforeBottom = 12;

        this.height_afterBottom = 12;
      }
    }
  }
  render() {
    let top_separator: any = (
      <Separator
        height_before={this.height_beforeTop}
        height_after={this.height_afterTop}
        key="top"
        position="top"
        TrackMouseUp={this.TrackMouseUp}
      />
    );
    let bottom_separator: any = (
      <Separator
        height_before={this.height_beforeBottom}
        height_after={this.height_afterBottom}
        key="bottom"
        position="bottom"
        TrackMouseUp={this.TrackMouseUp}
      />
    );

    if (!this.props.top) top_separator = null;
    if (!this.props.bottom) bottom_separator = null;

    let zIndex = this.props.url.length * 2;
    let style = {
      zIndex,
    };

    let overlayProps: any = {
      onMouseOver: this.handleMouseOver,
      onMouseOut: this.handleMouseOut,
    };
    if (this.props.type === 'close') overlayProps = {};
    return (
      <div
        className="layer_overlay"
        id="layer_overlay"
        style={style}
        // @ts-ignore
        ref={this.labelRef}
        {...overlayProps}
      >
        {top_separator}
        {bottom_separator}
      </div>
    );
  }
}

const mapDispatchToProps = {
  cx_handle_mouseup_layer: Action.cx_handle_mouseup_layer,
};

export default connect(null, mapDispatchToProps)(LayerDnd);
