import React, { Component } from 'react';
import Parent from './Parent';
import * as Helper from '../../helper/helper';
import { connect } from 'react-redux';
import { ThemeStore } from '../../redux/interfaces';

type TUrl = number | string;

interface IChildren {
  childs_unvisible: boolean;
  url: TUrl[];
}

// Type for url
type C = number | string;

interface IStateProps {
  childs: any | any[];
}

type Props = IStateProps & IChildren;

class Children extends Component<Props> {
  Components: any = [];

  conversion(props: Props) {
    let { childs_unvisible, url, childs } = props;

    this.Components = childs.map((item: any, index: number) => {
      let parent_props: any = {
        childs_unvisible,
        url: [...url, index],
        key: item,
      };

      if (index === childs.length - 1) {
        parent_props['last'] = true;
      }
      return <Parent {...parent_props} />;
    });
  }

  componentWillMount() {
    this.conversion(this.props);
  }

  shouldComponentUpdate(nextProps: Props) {
    let b = false;

    if (!keys_equal(this.props.childs, nextProps.childs)) {
      if (!b) b = true;
    }
    if (!Helper.arr_equal(this.props.url, nextProps.url)) {
      if (!b) b = true;
    }
    if (this.props.childs_unvisible !== nextProps.childs_unvisible) {
      if (!b) b = true;
    }
    if (!b) return false;

    this.conversion(nextProps);

    return b;
  }
  render() {
    return (
      <React.Fragment>
        <ul className="sub_dom">{this.Components}</ul>
      </React.Fragment>
    );
  }
}

const mapStateToProps = ({ dom }: ThemeStore, ownProps: IChildren) => {
  let { url } = ownProps;
  let childs = [];
  let item_key;

  if (dom.hasIn(url)) {
    childs = dom
      .getIn(url)
      .map((item: any) => {
        item_key = item.get('id');

        return item_key;
      })
      .toJS();
  }

  return {
    childs,
  };
};

function keys_equal(arr1: any, arr2: any) {
  if (arr1 instanceof Array && arr2 instanceof Array) {
    if (Object.entries(arr1).length !== Object.entries(arr2).length)
      return false;

    for (let i = 0; i < arr1.length; i++) {
      if (arr1[i] !== arr2[i]) return false;
    }
  } else {
    if (arr1 !== arr2) return false;
  }

  return true;
}

export default connect(mapStateToProps)(Children);
