import React, { Fragment } from 'react';
import Children from './Children';

function LayersList() {
  let children_props = {
    childs_unvisible: false,
    url: ['children'],
  };

  return (
    <Fragment>
      <ul className="dom_list">
        <Children {...children_props} />
      </ul>
    </Fragment>
  );
}

export default LayersList;
