import React, { Component, Fragment } from 'react';
import Children from './Children';
import Layer from './Layer';
import { connect } from 'react-redux';
import { ThemeStore, Turl } from '../../redux/interfaces';
import LayerClose from './LayerClose';
import store from '../../redux/store';

interface IParent {
  childs_unvisible: boolean;
  url: Turl;
  last?: boolean;
}
interface StateProps {
  bool_childs: boolean;
  txt: boolean;
  is: boolean;
  children_size: number;
}
interface IState {
  visible: boolean;
  open: boolean;
}
type Props = StateProps & IParent;

class Parent extends Component<Props> {
  constructor(props: Props) {
    super(props);
    this.handleChangeVisible = this.handleChangeVisible.bind(this);
    this.handleChangeOpen = this.handleChangeOpen.bind(this);
  }

  state: IState = {
    visible: true,
    open: false,
  };

  children_props: any = {};
  layer_props: any = {};
  closeLayer_props: any = {};
  childs: any = null;

  componentWillMount() {
    this.conversation(this.props, this.state);
  }
  componentDidMount() {
    let { dom } = store.getState();
    let elem = dom.getIn(this.props.url);

    if (elem) {
      let visible = elem.get('visible');
      let open = this.props.bool_childs ? elem.get('open') : false;

      this.setState({ visible, open });
    }
  }
  shouldComponentUpdate(nextProps: Props, nextState: IState) {
    if (!this.props.is) {
      return false;
    }

    this.conversation(nextProps, nextState);

    return true;
  }

  conversation(props: Props, state_props: IState) {
    let { childs_unvisible, url, bool_childs, last } = props;
    let { visible, open } = state_props;
    let c = !visible || childs_unvisible ? true : false;

    this.children_props = {
      childs_unvisible: c,
      url: [...url, 'children'],
    };

    this.layer_props = {
      parent: bool_childs,
      changeVisible: bool_childs
        ? this.handleChangeVisible.bind(this)
        : () => {},
      changeOpen: bool_childs ? this.handleChangeOpen.bind(this) : () => {},
      childs_unvisible,
      url,
    };

    this.closeLayer_props = {
      url,
      childs_unvisible,
      changeVisible: bool_childs
        ? this.handleChangeVisible.bind(this)
        : () => {},
    };

    if (last) {
      this.layer_props['last'] = true;
      this.closeLayer_props['last'] = true;
    }

    this.childs = open ? <Children {...this.children_props} /> : null;
  }

  handleChangeVisible(bool: boolean) {
    this.setState({ visible: bool });
  }
  handleChangeOpen(bool: boolean) {
    this.setState({ open: bool });
  }

  render() {
    let close_layer = null;

    if (this.state.open && !this.props.txt && this.props.children_size > 0) {
      close_layer = <LayerClose {...this.closeLayer_props} />;
    }

    return (
      <Fragment>
        <Layer {...this.layer_props} />
        {this.childs}
        {close_layer}
      </Fragment>
    );
  }
}
const mapStateToProps = ({ dom }: ThemeStore, ownProps: IParent) => {
  let { url } = ownProps;
  let is = false;
  let x: any = false;
  let txt = false;
  let children_size = 0;
  let item = dom.getIn(url);

  if (item) {
    is = true;
    x = item.get('children');
    txt = item.get('textContent');
    if (x) {
      children_size = x.size;
    }
  }
  return {
    is,
    bool_childs: x ? true : false,
    txt: txt ? true : false,
    children_size,
  };
};

export default connect(mapStateToProps)(Parent);
