import React, { Component, createRef } from 'react';

interface ISeparator {
  position: string;
  TrackMouseUp: (pos: string) => void;
  height_before: number;
  height_after: number;
}

class Separator extends Component<ISeparator> {
  separatorRef: any = createRef();
  componentDidMount() {
    let elem = this.separatorRef.current;

    elem.onmouseup = (e: any) => {
      e.stopPropagation();
      this.props.TrackMouseUp(this.props.position);
    };
  }
  render() {
    let classes = 'track ' + this.props.position;
    let styleBefore = {
      height: this.props.height_before + 'px',
    };
    let styleAfter = {
      height: this.props.height_after + 'px',
    };
    return (
      <div className={classes} ref={this.separatorRef}>
        <div className="before" style={styleBefore}></div>
        <div className="after" style={styleAfter}></div>
      </div>
    );
  }
}

export default Separator;
