export function arr_equal(arr1: any[], arr2: any[]) {
  if (arr1 instanceof Array && arr2 instanceof Array) {
    if (arr1.length === arr2.length) {
      return arr1.every((item: any, i: number) => {
        return item === arr2[i];
      });
    } else return false;
  }
}

export function pauseEvent(e: any) {
  if (e.stopPropagation) e.stopPropagation();
  if (e.preventDefault) e.preventDefault();
  e.cancelBubble = true;
  e.returnValue = false;
  return false;
}
export function sort_num(array: number[]) {
  array.sort(function (a: number, b: number) {
    return b - a;
  });
}
