import { fromJS, Map } from 'immutable';
import * as Data from '../../data/dom_functions';
import * as Helper from '../../helper/helper';
import { not_close_tags } from '../../data/dom';
import { ThemeStore, Turl } from '../../redux/interfaces';
import store from '../../redux/store';
import * as Clear from './clear_actions';

// @ts-ignore
import hotkeys from 'kai-hotkeys';
var VNode = require('virtual-dom/vnode/vnode');
var VText = require('virtual-dom/vnode/vtext');

var convertHTML = require('html-to-vdom')({
  VNode: VNode,
  VText: VText,
});

// Hotkeys
let hotkeys_object = {
  ctrl: false,
  shift: false,
};

document.addEventListener('keydown', (e: any) => {
  if (e.keyCode === 17) {
    hotkeys_object.ctrl = true;
  }
  if (e.keyCode === 16) {
    hotkeys_object.shift = true;
  }
});

document.addEventListener('keyup', (e: any) => {
  if (e.keyCode === 17) {
    hotkeys_object.ctrl = false;
  }
  if (e.keyCode === 16) {
    hotkeys_object.shift = false;
  }
});

hotkeys.add(
  'ctrl+c',
  (e: any, handler: any) => {
    e.preventDefault();

    cx_change_saved_items(store.dispatch, store.getState);
  },
  { oncePressing: true },
);

hotkeys.add(
  'ctrl+v',
  (e: any, handler: any) => {
    e.preventDefault();

    cx_insertion_elements(store.dispatch, store.getState);
  },
  { oncePressing: true },
);
hotkeys.add(
  'delete',
  (e: any, handler: any) => {
    e.preventDefault();

    cx_delete_elements(store.dispatch, store.getState);
  },
  { oncePressing: true },
);

//  
export const cx_delete_elements = (dispatch: any, getState: any) => {
  let { active_url, dom } = getState();

  let { nw_dom } = Data.delete_elements(dom, active_url);

  dispatch(Clear.change_store({ dom: nw_dom, active_url: [] }));
};

//  
export const cx_insertion_elements = (dispatch: any, getState: any) => {
  let { active_url, dom, saved_items } = getState();

  let { nw_dom, active_url2 } = Data.clone_bottom_elements(
    saved_items,
    dom,
    active_url,
  );

  dispatch(Clear.change_store({ dom: nw_dom, active_url: active_url2 }));
};

//    
export const cx_change_saved_items = (dispatch: any, getState: any) => {
  let { active_url, dom } = getState();
  let save_arr: any[] = [];
  let elem: any;

  active_url.forEach((item: any[]) => {
    elem = dom.getIn(item);
    if (elem) save_arr.push(elem);
  });

  dispatch(Clear.change_saved_items(save_arr));
};

//    
const delete_nested_active_url = (active_url: Turl[]): Turl[] => {
  let bool = false;
  let arr1: any = [];
  let copy = active_url.map((item: any[]) => [...item]);
  let arr_indexs: number[] = [];

  for (let i = 0; i < copy.length; i++) {
    for (let j = 0; j < copy.length; j++) {
      if (i !== j) {
        if (copy[i].length > copy[j].length) {
          arr1 = copy[j];
          bool = arr1.every((item: any, num: number) => item === copy[i][num]);
          if (bool) {
            arr_indexs.push(i);
          }
        } else {
          arr1 = copy[i];
          bool = arr1.every((item: any, num: number) => item === copy[j][num]);
          if (bool) {
            arr_indexs.push(j);
          }
        }
      }
    }
  }

  let uniqueArray = arr_indexs.filter(function (item, pos) {
    return arr_indexs.indexOf(item) === pos;
  });

  uniqueArray.sort(function (a: number, b: number) {
    return b - a;
  });

  uniqueArray.forEach((num: number) => {
    copy.splice(num, 1);
  });
  return copy;
};

//      
export const cx_active_mouseup = (url: Turl) => (
  dispatch: any,
  getState: any,
) => {
  let { active_url } = getState();

  if (active_url.length > 1) {
    if (!hotkeys_object.ctrl && !hotkeys_object.shift) {
      dispatch(Clear.change_active([url]));
    }
  }
};

//     Layer
export const cx_change_active = (url: Turl) => (
  dispatch: any,
  getState: any,
) => {
  let { active_url, dom } = getState();
  let copy_active_url = active_url.map((item: any[]) => [...item]);

  if (hotkeys_object.ctrl) {
    let index = -1;
    active_url.forEach((item: any, i: number) => {
      if (Helper.arr_equal(item, url)) {
        index = i;
      }
    });
    if (index !== -1) {
      copy_active_url.splice(index, 1);
    } else {
      copy_active_url.push(url);
    }

    dispatch(
      Clear.change_active(
        delete_nested_active_url(Data.clear_repeat_url(copy_active_url)),
      ),
    );
  } else if (hotkeys_object.shift) {
    let arr1 = active_url[active_url.length - 1].slice();
    let arr2 = [...url];

    let active_urls = Data.active_url_shift(arr1, arr2)(dom);
    if (active_urls.length > 0) {
      active_urls.forEach((active: Turl) => {
        copy_active_url.push(active);
      });

      dispatch(Clear.change_active(Data.clear_repeat_url(copy_active_url)));
    }
  } else {
    let bool = false;
    active_url.forEach((item: any[]) => {
      if (Helper.arr_equal(item, url)) bool = true;
    });
    if (!bool) dispatch(Clear.change_active([url]));
  }
};

// -----------------Complicated actions

//       this.Components
export const cx_move_items = (...data: any[]) => (
  dispatch: any,
  getState: () => ThemeStore,
) => {
  let { dom, active_url } = getState();
  let to = getState().to;

  if (data[0]) {
    to = data[0];
  }

  let index = -1;

  let a: any;
  if (to instanceof Object && to) {
    // **       to,      active_url
    active_url.forEach((item: any[], i: number) => {
      if (to instanceof Object && Helper.arr_equal(item, to.to)) {
        index = i;
      }
    });

    let copy_active_url = active_url.map((item) => item.slice());

    if (index > -1) {
      copy_active_url.splice(index, 1);
    }
    // ** <---      to,      active_url

    a = cxr_move_items(to.to, to.position)(dom, active_url);

    let obj = {
      dom: a.nw_dom,
      to: false,
      dnd: false,
      active_url: a.active_url,
    };
    dispatch(Clear.change_store(obj));
  }
};

//  dom   state.dom
export const cx_update_dom = (
  url: Turl,
  func: (item: any, url: Turl) => any,
) => (dispatch: any, getState: () => ThemeStore) => {
  let { dom } = getState();

  if (dom.getIn(url)) {
    let item = dom.getIn(url).toJS();
    let nw_item = func(item, url);
    let nw_dom = dom.updateIn(url, (value: any) => fromJS(nw_item));

    dispatch(Clear.change_dom(nw_dom));
  }
};

//  visible
export const change_visible = (url: Turl, value: boolean) => (
  dispatch: any,
  getState: () => ThemeStore,
) => {
  let { dom } = getState();
  let nw_dom = dom.updateIn([...url, 'visible'], (a: boolean) => value);
  dispatch(Clear.change_dom(nw_dom));
};

//  label
export const change_item = (url: Turl, label: string) => (dispatch: any) => {
  var vdom = convertHTML(label);
  let attrs: any = {};
  if (vdom.properties) {
    for (let key in vdom.properties) {
      if (key === 'attributes') {
        for (let key2 in vdom.properties[key]) {
          attrs[key2] = vdom.properties[key][key2];
        }
      } else {
        attrs[key] = vdom.properties[key];
      }
    }
  }

  function func(item: any, url: Turl) {
    let obj: any = {};
    let props: any = {
      open: item.open,
      visible: item.visible,
      id: item.id,
    };
    if (item.onlyText) props['onlyText'] = item.onlyText;
    if (item.children) props['children'] = item.children;

    if (vdom.tagName) {
      obj['tag'] = vdom.tagName;

      let bool_close_tag = not_close_tags.some((item) => item === obj['tag']);

      if (item.children === undefined && !bool_close_tag && !item.onlyText) {
        props['children'] = [];
      } else if (item.children && bool_close_tag) {
        delete props['children'];
      }
      if (vdom.children && !bool_close_tag) {
        if (vdom.children[0]) {
          if (vdom.children[0].text) {
            obj['textContent'] = vdom.children[0].text;
          }
        }
      }
    } else {
      delete props['children'];
      if (vdom.text) obj['textContent'] = vdom.text;
    }
    obj['attributes'] = attrs;

    return { ...props, ...obj };
  }

  dispatch(cx_update_dom(url, func));
};

//  open
export const change_open = (url: Turl, value: boolean) => (
  dispatch: any,
  getState: () => ThemeStore,
) => {
  let { dom } = getState();
  let nw_dom = dom.updateIn([...url, 'open'], (a: boolean) => value);
  dispatch(Clear.change_dom(nw_dom));
};

//     Layer
export const cx_handle_mouseup_layer = (url: Turl, position: string) => (
  dispatch: any,
  getState: () => ThemeStore,
) => {
  let { dom, active_url } = getState();
  let elem = dom.getIn(url);
  let open = elem.get('open');
  let children = elem.get('children');
  let b = true;

  if (active_url.length === 1) {
    if (Helper.arr_equal(active_url[0], url)) {
      b = false;
    }
  }
  if (b) {
    let data_to = {
      position: position === 'center' ? 'top' : position,
      to: position === 'center' ? [...url, 'children', 0] : url,
    };
    if (position === 'center') {
      if (children && open) {
        dispatch(Clear.change_to(data_to));
        dispatch(cx_move_items());
      } else if (children && !open) {
        dispatch(cx_move_items(data_to));
      }
    } else {
      dispatch(Clear.change_to(data_to));
      dispatch(cx_move_items());
    }
  }
  if (!b) dispatch(Clear.change_dnd(false));
};

export const cxr_move_items = (url: Turl, position: string) => (
  dom: any,
  active_url: Turl[],
) => {
  let obj: any = {};
  let copy_url = [...url];
  let to_url = [...url];
  let nw_dom = dom;

  let mas_pos = ['top', 'bottom'];

  //   
  let actvs = active_url.map((item: any[]) => {
    return [...item];
  });

  //      ,    textContent
  //    ,   textContent
  copy_url.pop();
  copy_url.pop();
  let to_elem = dom.getIn(copy_url);
  if (to_elem) {
    if (
      to_elem.get('children').size === 0 &&
      to_elem.has('textContent') &&
      !to_elem.get('open')
    ) {
      if (position === 'top' && to_url[to_url.length - 1] === 0) {
        to_url[to_url.length - 1] = 1;

        let textcontent_obj = {
          open: false,
          visible: true,
          id: `${Math.random()} ${[...copy_url, 'children', 0].join('-')}`,
          textContent: to_elem.get('textContent'),
        };
        nw_dom = nw_dom.setIn([...copy_url, 'open'], true);
        nw_dom = nw_dom.deleteIn([...copy_url, 'textContent']);
        nw_dom = nw_dom.setIn(
          [...copy_url, 'children', 0],
          Map(textcontent_obj),
        );
      }
    } else if (to_elem.get('children') && !to_elem.get('open')) {
      nw_dom = nw_dom.setIn([...copy_url, 'open'], true);
    }
  }
  // </    ,   textContent >

  mas_pos.forEach((name: string) => {
    if (name === position) {
      // @ts-ignore
      obj = Data.InsertElement[name](to_url, nw_dom, actvs);
    }
  });

  return {
    nw_dom: obj.nw_dom,
    active_url: obj.active_url2,
  };
};
