import React from 'react';
import './LabelPanel.sass';
import close_url from '../../assets/svg/close.svg';

interface ILabelPanel {
  text: string;
  icon: string;
  close: boolean;
}

function LabelPanel({ text, icon, close }: ILabelPanel) {
  return (
    <div className="modular_label">
      <div className="ml_wrap">
        <img src={icon} alt="DOM" />
        <span className="title">{text}</span>
      </div>
      {close && (
        <img src={close_url} className="close" alt="modal window close" />
      )}
    </div>
  );
}

export default LabelPanel;
