import { Turl, Tto } from '../../redux/interfaces';

// Simple actions
export const change_saved_items = (items: any[]) => {
  return {
    type: 'CHANGE_SAVED_ITEMS',
    payload: items,
  };
};
export const change_store = (obj: any) => {
  return {
    type: 'CHANGE_STORE',
    payload: obj,
  };
};
export const change_to = (url: Tto) => {
  return {
    type: 'CHANGE_TO',
    payload: url,
  };
};
export const change_mode_save = (bool: boolean) => {
  return {
    type: 'CHANGE_MODE_SAVE',
    payload: bool,
  };
};
export const change_count_items = (num: number) => {
  return {
    type: 'CHANGE_COUNT_ITEMS',
    payload: num,
  };
};

export const change_active = (active_url: Turl[]) => {
  return {
    type: 'CHANGE_ACTIVE',
    payload: active_url,
  };
};

export const change_dnd = (bool: boolean) => {
  return {
    type: 'CHANGE_DND',
    payload: bool,
  };
};
export const change_move = (bool: boolean) => {
  return {
    type: 'CHANGE_MOVE',
    payload: bool,
  };
};

export const change_dom = (dom: any) => {
  return {
    type: 'CHANGE_DOM',
    payload: dom,
  };
};
