import React, { Component } from 'react';
import Editor from 'react-simple-code-editor';
import Prism from 'prismjs';
// @ts-ignore
import { highlight } from 'prismjs/components/prism-core';

interface ILabel {
  label: string;
  edit: boolean;
  changeLabel: (label: string) => void;
}
interface IState {
  label: string;
}

class Label extends Component<ILabel> {
  constructor(props: ILabel) {
    super(props);

    this.handleChangeLabel = this.handleChangeLabel.bind(this);
  }
  state = {
    label: this.props.label,
  };

  handleChangeLabel(label: string) {
    this.setState({ label });
    this.props.changeLabel(label);
  }
  componentDidUpdate(prevProps: ILabel, prevState: IState) {
    let label = this.props.label;
    if (prevProps.label !== label) {
      this.setState({ label });
    }
  }

  render() {
    return (
      <div className="label">
        <Editor
          value={this.state.label}
          onValueChange={(code) => this.handleChangeLabel(code)}
          highlight={(code) => highlight(code, Prism.languages.html)}
          padding={10}
          style={{
            fontFamily: '"Fira code", "Fira Mono", monospace',
            fontSize: 14,
          }}
        />
      </div>
    );
  }
}

export default Label;
