import { ThemeAction, ThemeStore } from './interfaces';
import { fromJS } from 'immutable';
import dom from '../data/dom';

export const InitialState: ThemeStore = {
  dom: fromJS(dom),
  dnd: false,
  move: false,
  active_url: [],
  to: false,
  saved_items: [],
  mode_save: false,
  count_items: 0,
};

const reducer = (state: any = InitialState, action: ThemeAction) => {
  switch (action.type) {
    case 'CHANGE_STORE': {
      return {
        ...state,
        ...action.payload,
      };
    }
    case 'CHANGE_DND': {
      return {
        ...state,
        dnd: action.payload,
      };
    }
    case 'CHANGE_MODE_SAVE': {
      return {
        ...state,
        mode_save: action.payload,
      };
    }
    case 'CHANGE_COUNT_ITEMS': {
      return {
        ...state,
        count_items: action.payload,
      };
    }
    case 'CHANGE_MOVE': {
      return {
        ...state,
        move: action.payload,
      };
    }
    case 'CHANGE_SAVED_ITEMS': {
      return {
        ...state,
        saved_items: action.payload,
      };
    }
    case 'CHANGE_TO': {
      return {
        ...state,
        to: action.payload,
      };
    }
    case 'CHANGE_DOM': {
      return {
        ...state,
        dom: action.payload,
      };
    }
    case 'CHANGE_ACTIVE': {
      return {
        ...state,
        active_url: action.payload,
      };
    }

    default:
      return state;
  }
};

export default reducer;
